import React, {useEffect} from 'react';
import Checkout from "./Checkout";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Header from "./Header";
import Home from "./Home";
import './App.css';
import Login from './Login';
import auth from "./firebase";
import useStateValue from './StateProvider';

function App() {
  const [{user}, dispatch]= useStateValue();

 useEffect(() => {
  const unsubscribe = auth.onAuthStateChanged((authUser) => {
    if(authUser){
        dispatch({
          type: "SET_USER",
          user : authUser,
        });
    }else{
      dispatch({
        type: "SET_USER",
        user : null,
      });
    }
   });
   return ()=>{
    unsubscribe();
   }
 }, []);


// useEffect(() => {
//   effect
//   return () => {
//     cleanup
//   }
// }, [input])



  return (
    <Router>
    <div className="App">
    <Switch>
      <Route path="/checkout">
          <Header />
          <Checkout />
       
      </Route>
      <Route path="/login">
       <Login />
      </Route>
      <Route path="/">
          <Header />
          <Home />
      </Route>
     
      </Switch>
    </div>
    </Router>
  );
}

export default App;
